import {request} from './request'

//获得商标采购单
export async function GetAllBrands(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/GetAllBrands',
        data:data
    })
}
//增加商标采购单
export async function InsertOrUpdateBrand(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/InsertOrUpdateBrand',
        data:data
    })
}
//根据关联ID获取指定的数据
export async function GetRelatedBrands(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/GetRelatedBrands',
        data:data
    })
}
//删除商标采购单
export async function DeleteBrand(data){
    return await request({
        method:'DELETE',
        url:'/api/BusinessUnit/DeleteBrand',
        data:data
    })
}