<template>
  <div id="summary">
    <!-- 操作区 -->
    <el-row class="text-right">
      <el-button type="primary" @click="insert">添加</el-button>
      <el-button type="danger" @click="remove">删除</el-button>
    </el-row>
    <!-- 表格区 -->
    <el-row id="plan">
      <el-table @select="rowclick" border :data="tableData">
        <el-table-column label="商标采购计划汇总表">
           <el-table-column type="selection" width="50"></el-table-column>
          <el-table-column label="序号" type="index" width="50"></el-table-column>
          <el-table-column
            :label="item.label"
            :width="item.width"
            v-for="item in tableHeaderItems"
            :key="item.index"
          >
            <template slot-scope="scope">
              <el-input v-model="scope.row[item.prop]"></el-input>
            </template>
          </el-table-column>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row>
      <!-- 提交区 -->
      <el-col class="text-right" >
          <el-button type="primary" @click="submit">提交</el-button>
          <el-button type="warning" @click="cancel">取消</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {GetRelatedBrands,DeleteBrand,InsertOrUpdateBrand} from "network/mark.js";
import {arraySort} from "service/dateUtil.js"

import {TableItem} from "service/Business/mark.js"
export default {
  data() {
    return {
      tableData: [
      ],
      tableHeaderItems: [
        {
          label: "订单号",
          width: "150",
          prop: "orderNumber",
        },
        {
          label: "业务员",
          width: "150",
          prop: "salesManName",
        },
        {
          label: "客户",
          width: "150",
          prop: "customerName",
        },
        {
          label: "货号",
          width: "150",
          prop: "productId",
        },
        {
          label: "种类",
          width: "150",
          prop: "brandType",
        },
        {
          label: "数量",
          width: "150",
          prop: "number",
        },
        {
          label: "交货日期",
          width: "150",
          prop: "requiredTime",
        },
        {
          label: "采购方",
          width: "150",
          prop: "purchaser",
        },
        {
          label: "供应商",
          width: "150",
          prop: "supplier",
        },
        {
          label: "下单日期",
          width: "150",
          prop: "creationTime",
        },
        {
          label: "单价",
          width: "150",
          prop: "price",
        },
        {
          label: "备注",
          width: "150",
          prop: "comment2",
        }
      ],
      selectData: [],
      deleteData: [],
      getRelatedBrand:{
        guid: "",
        pageSize: 10,
        pageNumber: 1,
      },
      dialogImageUrl:'',
      dialogVisible:false,
      isContainBlank:false
    };
  },
  created(){
    this.inital();
  },
  methods:{
     //初始化
    inital() {
      //判断新建还是编辑
      //新建
      if(this.$route.query.iid === undefined){}
      //编辑
      else{this.getRelatedBrand.guid = this.$route.query.iid;
      GetRelatedBrands(this.getRelatedBrand).then((res) => {
        this.tableData = res.result.brandDto;
        this.tableData.forEach((item, index) => {
          item.index = index;
        });
      })}
    },
    //选择选框
    rowclick(selection, row) {
      this.selectData = selection;
    },
    //添加按钮
    insert(){
      const tableItem = new TableItem(this.getRelatedBrand.guid);
      this.tableData.push(tableItem);
      this.tableData.forEach((item, index) => {
          item.index = index;
      });
    },
    //删除按钮
    remove(){
      const data = this.selectData;
      //判断是否为空
       if (data.length == 0) {
        this.$message.error("请先选择删除项！");
      } else {
        //arr存放传给后台删除id，arr1存放界面删除的索引
        let arr = new Array(data.length);
        let arr1 = new Array(data.length);
        data.forEach((item, index) => {
          arr[index] = item.brand_Order_DetailId;
          arr1[index] = item.index;
        });
        //排序
        arr1 = arraySort(arr1);
        //对界面数据进行删除
        arr1.forEach((item) => {
          this.tableData.splice(item, 1);
        });
        this.deleteData = arr;
      }
    },
    //删除操作
    delete(){
      DeleteBrand(this.deleteData).then((res) => {
        if (res.success) {
          this.$message.success("删除成功");
        }
      });
    },
    //校验
    validation(){
      try{
      this.tableData.forEach((item,index)=>{
        for(var i in item){
          if(item[i]===""){
            this.$message.error("第"+(index+1)+"行含有空字符串");
            this.isContainBlank = true;
            throw new Error('End Loop');
          }
          else{
             this.isContainBlank = false;
          }
        }
      })}catch(e){
        //catch语句跳出forEach循环
      }
    },
    //提交操作
    submit(){
      //校验有没有空数据
     /*  this.validation(); */
      if(!this.isContainBlank){
        this.delete();
        InsertOrUpdateBrand(this.tableData).then(res=>{
          if(res.success){
            this.$router.push("/mark")
          }
        });
      }
    },
    //取消
    cancel(){
      this.$router.go(-1);
    },
      handlePictureCardPreview(file){
          this.dialogImageUrl = file.url;
          this.dialogVisible = true
      }
  }
};
</script>

<style scoped>
.el-row {
  margin-bottom: 10px;
}
</style>